.image-6-icon {
  height: 32px;
  width: 32px;
  position: relative;
  object-fit: cover;
  min-height: 32px;
}
.fusion-engage {
  align-self: stretch;
  position: relative;
  line-height: 28px;
  font-weight: 500;
  white-space: nowrap;
}
.fusion-engage-wrapper,
.main-f-r-a-m-e {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.fusion-engage-wrapper {
  width: 140px;
  flex-direction: column;
  padding: var(--padding-11xs) 0 0;
}
.main-f-r-a-m-e {
  align-self: stretch;
  height: 64px;
  background-color: var(--light-white);
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.12);
  flex-direction: row;
  padding: var(--padding-base) var(--padding-61xl);
  gap: var(--gap-5xs);
  top: 0;
  z-index: 99;
  position: sticky;
  text-align: center;
  font-size: var(--desktop-h5-medium-size);
  color: var(--color-black);
  font-family: var(--desktop-b1-bold);
}
.bg,
.welcome-to {
  position: relative;
}
.bg {
  align-self: stretch;
  width: 1120px;
  border-radius: var(--br-13xl);
  background-color: var(--secondary);
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.08);
  display: none;
  max-width: 100%;
}
.welcome-to {
  line-height: 32px;
  font-weight: 500;
}
.image-6-icon1 {
  height: 32px;
  width: 32px;
  position: relative;
  object-fit: cover;
  min-height: 32px;
}
.fusion-engage1 {
  flex: 1;
  position: relative;
  line-height: 32px;
  font-weight: 600;
}
.image-6-parent {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs);
  text-align: center;
  color: var(--primary-p1);
  font-family: var(--desktop-b1-bold);
}
.welcome-to-parent {
  width: 230px;
  height: 76px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-xs);
}
.sign-up-as {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 56px;
  font-weight: 700;
  font-family: inherit;
}
.call-to-action {
  width: 237px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
}
.begin-your-journey {
  align-self: stretch;
  position: relative;
  font-size: var(--desktop-h4-inter-regular-size);
  line-height: 32px;
  font-family: var(--desktop-b1-bold);
  color: var(--grey-grey-1);
  text-align: left;
}
.call-to-action-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-base);
}
.login-with-google {
  height: 24px;
  width: 24px;
  position: relative;
  min-height: 24px;
}
.sign-in {
  position: relative;
  font-size: var(--desktop-b1-bold-size);
  line-height: 24px;
  display: inline-block;
  font-family: var(--desktop-b1-bold);
  color: var(--light-white);
  text-align: left;
  min-width: 53px;
}
.login-options {
  cursor: pointer;
  border: 0;
  padding: var(--padding-sm) var(--padding-xl);
  background-color: var(--grey-black-1);
  width: 500px;
  height: 52px;
  border-radius: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  gap: var(--gap-xs);
  white-space: nowrap;
  max-width: 100%;
}
.login-options:hover {
  background-color: #474747;
}
.frame-group {
  align-self: stretch;
  gap: var(--gap-21xl);
  text-align: center;
  font-size: var(--desktop-h2-bold-size);
}
.frame-group,
.frame-parent,
.frame-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.frame-parent {
  align-self: stretch;
  gap: var(--gap-45xl);
  z-index: 2;
}
.frame-wrapper {
  flex: 1;
  padding: var(--padding-29xl) 0 0;
  box-sizing: border-box;
  min-width: 342px;
}
.frame-child,
.image-8-icon {
  position: relative;
  max-width: 100%;
}
.frame-child {
  height: 540px;
  width: 500px;
  border-radius: 0 var(--br-13xl) var(--br-13xl) 0;
  background-color: var(--color-steelblue);
  display: none;
}
.image-8-icon {
  height: 206.2px;
  flex: 1;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.rectangle-parent {
  height: 540px;
  width: 500px;
  border-radius: 0 var(--br-13xl) var(--br-13xl) 0;
  background-color: var(--color-steelblue);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 167px var(--padding-13xl) 166.8px;
  box-sizing: border-box;
  min-width: 500px;
  max-width: 100%;
  z-index: 1;
}
.bg-parent,
.frame-item {
  flex-shrink: 0;
  debug_commit: f6aba90;
}
.bg-parent {
  width: 1120px;
  border-radius: var(--br-13xl);
  background-color: var(--secondary);
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 0 0 var(--padding-29xl);
  box-sizing: border-box;
  gap: 46px;
}
.frame-item {
  align-self: stretch;
  height: 14.7px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
}
.secondary-f-r-a-m-e-inner {
  height: 177.7px;
  width: 238px;
  flex-direction: column;
  align-items: flex-start;
  padding: 163px 0 0;
}
.account,
.secondary-f-r-a-m-e,
.secondary-f-r-a-m-e-inner {
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
}
.secondary-f-r-a-m-e {
  width: 1220px;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 0 0 var(--padding-xl);
  gap: 80px;
  max-width: 100%;
  text-align: left;
  font-size: var(--desktop-h4-inter-regular-size);
  color: var(--grey-black-1);
  font-family: var(--desktop-h2-bold);
}
.account {
  width: 100%;
  position: relative;
  background-color: var(--light-white);
  overflow: hidden;
  flex-direction: column;
  align-items: flex-end;
  padding: 0 0 395px;
  gap: var(--gap-45xl);
  letter-spacing: normal;
}
@media screen and (max-width: 1225px) {
  .bg-parent {
    padding-left: var(--padding-5xl);
    box-sizing: border-box;
  }
  .secondary-f-r-a-m-e {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 1050px) {
  .image-8-icon,
  .rectangle-parent {
    flex: 1;
  }
  .bg-parent {
    flex-wrap: wrap;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .sign-up-as {
    font-size: var(--desktop-h3-inter-medium-size);
    line-height: 45px;
  }
  .frame-group {
    gap: var(--gap-xl);
  }
  .frame-parent {
    gap: var(--gap-13xl);
  }
  .rectangle-parent {
    padding-top: 109px;
    padding-bottom: 108px;
    box-sizing: border-box;
    min-width: 100%;
  }
  .bg-parent {
    gap: 23px;
  }
  .secondary-f-r-a-m-e {
    gap: var(--gap-21xl);
  }
  .account {
    gap: var(--gap-13xl);
  }
}
@media screen and (max-width: 450px) {
  .main-f-r-a-m-e {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .fusion-engage1,
  .welcome-to {
    font-size: var(--font-size-lgi);
    line-height: 26px;
  }
  .sign-up-as {
    font-size: var(--desktop-h4-inter-regular-size);
    line-height: 34px;
  }
  .begin-your-journey {
    font-size: var(--font-size-lgi);
    line-height: 26px;
  }
  .frame-parent {
    gap: var(--gap-base);
  }
  .frame-wrapper {
    padding-top: 31px;
    box-sizing: border-box;
    min-width: 100%;
  }
  .secondary-f-r-a-m-e {
    gap: var(--gap-xl);
  }
  .account {
    gap: var(--gap-base);
  }
}
